import React, { useState } from 'react';
import workData from '../../data/work.json';
import FancyCard from './FancyCard';

const Tab1 = () => {
    return (
        <div id="tab-1">
            {workData['wordpress'].map((post, i) => {
                return (<FancyCard key={i} data={post} />)
            })}
        </div>
    );
}
// const Tab2 = () => {
//     return (
//         <div id="tab-2">
//             {workData['reactjs'].map((post, i) => {
//                 return (<FancyCard key={i} data={post} />)
//             })}
//         </div>
//     );
// }

export default function Work() {
    const [activeTab, setActiveTab] = useState(1);

    return (<div className="cont" id="workP">
       <center> <h1>Work</h1> </center>
        <div className="selector">
            {/* <button className={activeTab === 0 ? 'active' : 'not-active'} onClick={() => setActiveTab(0)} >All</button> */}
            <button className={activeTab === 1 ? 'active' : 'not-active'} onClick={() => setActiveTab(1)} >Wordpress</button>
            {/* <button className={activeTab === 2 ? 'active' : 'not-active'} onClick={() => setActiveTab(2)} >ReactJs</button> */}
            {/* <button className={activeTab === 3 ? 'active' : 'not-active'} onClick={() => setActiveTab(3)} >UX & UI design</button> */}
        </div>
        <div className="content">
            {activeTab === 0 ? <Tab0 /> : ''}
            {activeTab === 1 ? <Tab1 /> : ''}
            {activeTab === 2 ? <Tab2 /> : ''}
            {activeTab === 3 ? <Tab3 /> : ''}
        </div>
        <style jsx>{`
            .cont{
                width: 100%;
                position: relative;
               color: #ffffff;
               height: auto;
               background: #010101;
               padding: 30px 10%;
               padding-bottom: 100px;
            }
            .selector {
                margin: 40px 100px;
            }
            .selector button {
                cursor: pointer;
                background: none;
                color: #fff;
                border: 1px solid #ffff00;
                border-radius: 100px;
                padding: 12px 20px;
                font-size: 18px;
                margin: 0 10px;
                font-weight: bold;
                transition-duration: 300ms;
                outline: none;
            }
            .selector button.active {
                background: #ffff00;
                color: #000000;
            }
            `}</style>
    </div>
    )
}